'use client';

import { useState } from 'react';
import { NavigationTab } from '@/types';
import BottomNav from '@/components/bottom-nav';
import ChefsJourney from '@/components/chefs-journey';
import KitchenFeed from '@/components/kitchen-feed';
import RecipeCards from '@/components/recipe-cards';
import ChefsShowdown from '@/components/chefs-showdown';
import Profile from '@/components/profile';

export default function Home() {
  const [activeTab, setActiveTab] = useState<NavigationTab>('journey');

  const renderActiveComponent = () => {
    switch (activeTab) {
      case 'journey':
        return <ChefsJourney />;
      case 'feed':
        return <KitchenFeed />;
      case 'recipes':
        return <RecipeCards />;
      case 'challenges':
        return <ChefsShowdown />;
      case 'profile':
        return <Profile />;
      default:
        return <ChefsJourney />;
    }
  };

  return (
    <div className="relative">
      {/* App Header */}
      <header className="sticky top-0 z-10 bg-white/95 backdrop-blur-sm border-b border-stone-200 px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-orange-400 to-emerald-400 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-sm">T</span>
            </div>
            <h1 className="text-xl font-bold text-stone-800">TableTalk</h1>
          </div>
          
          <div className="flex items-center space-x-3">
            <button className="text-stone-600 hover:text-stone-800 transition-colors">
              <span className="text-lg">üîç</span>
            </button>
            <button className="text-stone-600 hover:text-stone-800 transition-colors">
              <span className="text-lg">üîî</span>
            </button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="min-h-screen">
        {renderActiveComponent()}
      </main>

      {/* Bottom Navigation */}
      <BottomNav activeTab={activeTab} onTabChange={setActiveTab} />
    </div>
  );
}
