'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { mockRecipes } from '@/data/mock-data';
import { Recipe } from '@/types';

export default function RecipeCards() {
  const [selectedRecipe, setSelectedRecipe] = useState<Recipe | null>(null);
  const [currentStep, setCurrentStep] = useState(0);

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'Easy': return 'bg-emerald-100 text-emerald-700';
      case 'Medium': return 'bg-orange-100 text-orange-700';
      case 'Hard': return 'bg-red-100 text-red-700';
      default: return 'bg-stone-100 text-stone-700';
    }
  };

  const handleRecipeSelect = (recipe: Recipe) => {
    setSelectedRecipe(recipe);
    setCurrentStep(0);
  };

  const nextStep = () => {
    if (selectedRecipe && currentStep < selectedRecipe.steps.length - 1) {
      setCurrentStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  };

  if (selectedRecipe) {
    return (
      <div className="space-y-4 pb-20">
        {/* Header */}
        <div className="bg-gradient-to-r from-orange-50 to-emerald-50 p-6 rounded-b-3xl">
          <div className="flex items-center justify-between mb-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setSelectedRecipe(null)}
              className="text-stone-600 hover:text-stone-800"
            >
              ‚Üê Back to Recipes
            </Button>
            <div className="flex items-center space-x-2">
              <span className="text-red-500 text-lg">‚ù§Ô∏è</span>
              <span className="text-sm font-medium">{selectedRecipe.likes}</span>
            </div>
          </div>
          <h1 className="text-2xl font-bold text-stone-800">{selectedRecipe.title}</h1>
          <p className="text-stone-600">{selectedRecipe.description}</p>
        </div>

        {/* Recipe Info */}
        <div className="px-4">
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center justify-between mb-4">
                <div className="flex items-center space-x-4">
                  <div className="text-center">
                    <div className="text-lg">‚è±Ô∏è</div>
                    <div className="text-xs text-stone-600">{selectedRecipe.cookingTime}min</div>
                  </div>
                  <div className="text-center">
                    <Badge className={getDifficultyColor(selectedRecipe.difficulty)}>
                      {selectedRecipe.difficulty}
                    </Badge>
                  </div>
                </div>
                <div className="flex items-center space-x-2">
                  <Button size="sm" variant="outline" className="text-orange-600 border-orange-200">
                    üìå Save
                  </Button>
                  <Button size="sm" variant="outline" className="text-emerald-600 border-emerald-200">
                    üì§ Share
                  </Button>
                </div>
              </div>

              {/* Tags */}
              <div className="flex flex-wrap gap-2">
                {selectedRecipe.tags.map((tag) => (
                  <Badge key={tag} variant="outline" className="text-xs">
                    #{tag}
                  </Badge>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Step-by-Step Instructions */}
        <div className="px-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Instructions</span>
                <Badge variant="secondary">
                  Step {currentStep + 1} of {selectedRecipe.steps.length}
                </Badge>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {/* Current Step */}
              <div className="bg-gradient-to-br from-orange-50 to-emerald-50 p-4 rounded-lg">
                <div className="flex items-start space-x-3">
                  <div className="w-8 h-8 bg-orange-500 text-white rounded-full flex items-center justify-center font-semibold text-sm">
                    {currentStep + 1}
                  </div>
                  <div className="flex-1">
                    <p className="text-stone-800 font-medium mb-2">
                      {selectedRecipe.steps[currentStep].instruction}
                    </p>
                    {selectedRecipe.steps[currentStep].hasVideo && (
                      <div className="bg-white/80 rounded-lg p-3 text-center">
                        <div className="text-4xl mb-2">üé•</div>
                        <p className="text-xs text-stone-600">Video demonstration available</p>
                      </div>
                    )}
                  </div>
                </div>
              </div>

              {/* Navigation */}
              <div className="flex items-center justify-between">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={prevStep}
                  disabled={currentStep === 0}
                >
                  ‚Üê Previous
                </Button>
                <div className="flex space-x-1">
                  {selectedRecipe.steps.map((_, index) => (
                    <button
                      key={index}
                      onClick={() => setCurrentStep(index)}
                      className={`w-2 h-2 rounded-full transition-colors ${
                        index === currentStep ? 'bg-orange-500' : 'bg-stone-300'
                      }`}
                    />
                  ))}
                </div>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={nextStep}
                  disabled={currentStep === selectedRecipe.steps.length - 1}
                >
                  Next ‚Üí
                </Button>
              </div>

              {/* Completion */}
              {currentStep === selectedRecipe.steps.length - 1 && (
                <div className="text-center p-4 bg-emerald-50 rounded-lg">
                  <div className="text-3xl mb-2">üéâ</div>
                  <p className="font-semibold text-emerald-700">Recipe Complete!</p>
                  <p className="text-sm text-emerald-600 mb-3">Share your creation with the community</p>
                  <Button className="bg-emerald-500 hover:bg-emerald-600">
                    Share My Dish
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6 pb-20">
      {/* Header */}
      <div className="bg-gradient-to-r from-orange-50 to-emerald-50 p-6 rounded-b-3xl">
        <h1 className="text-2xl font-bold text-stone-800">Recipe Collection</h1>
        <p className="text-stone-600">Discover and master new dishes</p>
      </div>

      {/* Filter Tabs */}
      <div className="px-4">
        <Tabs defaultValue="all" className="w-full">
          <TabsList className="grid w-full grid-cols-4 bg-stone-100">
            <TabsTrigger value="all" className="text-xs">All</TabsTrigger>
            <TabsTrigger value="easy" className="text-xs">Easy</TabsTrigger>
            <TabsTrigger value="quick" className="text-xs">Quick</TabsTrigger>
            <TabsTrigger value="saved" className="text-xs">Saved</TabsTrigger>
          </TabsList>

          <TabsContent value="all" className="space-y-4 mt-6">
            {mockRecipes.map((recipe) => (
              <Card
                key={recipe.id}
                className="cursor-pointer hover:shadow-md transition-shadow"
                onClick={() => handleRecipeSelect(recipe)}
              >
                <CardContent className="p-0">
                  {/* Recipe Image */}
                  <div className="aspect-video bg-gradient-to-br from-orange-100 via-stone-100 to-emerald-100 flex items-center justify-center rounded-t-lg">
                    <div className="text-center">
                      <div className="text-4xl mb-2">üçΩÔ∏è</div>
                      <p className="text-stone-600 font-medium">{recipe.title}</p>
                    </div>
                  </div>

                  {/* Recipe Info */}
                  <div className="p-4">
                    <div className="flex items-start justify-between mb-2">
                      <div>
                        <h3 className="font-semibold text-stone-800 mb-1">{recipe.title}</h3>
                        <p className="text-sm text-stone-600 line-clamp-2">{recipe.description}</p>
                      </div>
                    </div>

                    <div className="flex items-center justify-between mt-3">
                      <div className="flex items-center space-x-3">
                        <div className="flex items-center space-x-1 text-stone-500">
                          <span className="text-sm">‚è±Ô∏è</span>
                          <span className="text-xs">{recipe.cookingTime}min</span>
                        </div>
                        <Badge className={getDifficultyColor(recipe.difficulty)} variant="secondary">
                          {recipe.difficulty}
                        </Badge>
                      </div>

                      <div className="flex items-center space-x-3 text-stone-500">
                        <div className="flex items-center space-x-1">
                          <span className="text-sm">‚ù§Ô∏è</span>
                          <span className="text-xs">{recipe.likes}</span>
                        </div>
                        <div className="flex items-center space-x-1">
                          <span className="text-sm">üìå</span>
                          <span className="text-xs">{recipe.saves}</span>
                        </div>
                      </div>
                    </div>

                    {/* Tags */}
                    <div className="flex flex-wrap gap-1 mt-3">
                      {recipe.tags.slice(0, 3).map((tag) => (
                        <Badge key={tag} variant="outline" className="text-xs">
                          #{tag}
                        </Badge>
                      ))}
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </TabsContent>

          <TabsContent value="easy" className="space-y-4 mt-6">
            <div className="text-center py-8 text-stone-500">
              <div className="text-4xl mb-2">üîç</div>
              <p>Easy recipes coming soon!</p>
            </div>
          </TabsContent>

          <TabsContent value="quick" className="space-y-4 mt-6">
            <div className="text-center py-8 text-stone-500">
              <div className="text-4xl mb-2">‚ö°</div>
              <p>Quick recipes coming soon!</p>
            </div>
          </TabsContent>

          <TabsContent value="saved" className="space-y-4 mt-6">
            <div className="text-center py-8 text-stone-500">
              <div className="text-4xl mb-2">üìå</div>
              <p>Your saved recipes will appear here</p>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
