'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { Button } from '@/components/ui/button';
import { mockUser, mockSkillNodes, mockBadges } from '@/data/mock-data';
import { SkillNode } from '@/types';

export default function ChefsJourney() {
  const [selectedNode, setSelectedNode] = useState<SkillNode | null>(null);
  const [showLevelSelector, setShowLevelSelector] = useState(false);

  const progressToNextLevel = ((mockUser.chefLevel % 5) / 5) * 100;
  const earnedBadges = mockBadges.filter(badge => badge.earned);
  const availableBadges = mockBadges.filter(badge => !badge.earned);

  const handleNodeClick = (node: SkillNode) => {
    if (node.isUnlocked) {
      setSelectedNode(node);
    }
  };

  const getLevelColor = (level: string) => {
    switch (level) {
      case 'Beginner': return 'bg-emerald-100 text-emerald-700';
      case 'Intermediate': return 'bg-orange-100 text-orange-700';
      case 'Advanced': return 'bg-purple-100 text-purple-700';
      default: return 'bg-stone-100 text-stone-700';
    }
  };

  return (
    <div className="space-y-6 pb-20">
      {/* Header */}
      <div className="bg-gradient-to-r from-orange-50 to-emerald-50 p-6 rounded-b-3xl">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-2xl font-bold text-stone-800">Chef's Journey</h1>
            <p className="text-stone-600">Level up your culinary skills</p>
          </div>
          <Button
            variant="outline"
            size="sm"
            onClick={() => setShowLevelSelector(!showLevelSelector)}
            className={`${getLevelColor(mockUser.level)} border-0`}
          >
            {mockUser.level}
          </Button>
        </div>

        {/* Progress Card */}
        <Card className="bg-white/80 backdrop-blur-sm border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center justify-between mb-2">
              <span className="text-sm font-medium text-stone-700">Chef Level {mockUser.chefLevel}</span>
              <span className="text-xs text-stone-500">{Math.round(progressToNextLevel)}% to next level</span>
            </div>
            <Progress value={progressToNextLevel} className="h-2 mb-3" />
            <div className="flex justify-between text-xs text-stone-600">
              <span>{mockUser.completedRecipes} recipes completed</span>
              <span>{earnedBadges.length} badges earned</span>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Level Selector */}
      {showLevelSelector && (
        <Card className="mx-4">
          <CardHeader>
            <CardTitle className="text-lg">Choose Your Starting Level</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            {['Beginner', 'Intermediate', 'Advanced'].map((level) => (
              <Button
                key={level}
                variant={mockUser.level === level ? 'default' : 'outline'}
                className="w-full justify-start"
                onClick={() => setShowLevelSelector(false)}
              >
                <span className="mr-2">
                  {level === 'Beginner' ? 'üå±' : level === 'Intermediate' ? 'üî•' : '‚≠ê'}
                </span>
                {level}
              </Button>
            ))}
          </CardContent>
        </Card>
      )}

      {/* Skill Tree */}
      <div className="px-4">
        <h2 className="text-xl font-semibold text-stone-800 mb-4">Skill Tree</h2>
        <Card className="p-4 bg-gradient-to-br from-stone-50 to-emerald-50">
          <div className="relative h-64 overflow-hidden">
            {mockSkillNodes.map((node) => (
              <button
                key={node.id}
                onClick={() => handleNodeClick(node)}
                className={`absolute transform -translate-x-1/2 -translate-y-1/2 w-16 h-16 rounded-full border-2 transition-all duration-200 ${
                  node.isCompleted
                    ? 'bg-emerald-500 border-emerald-600 text-white'
                    : node.isUnlocked
                    ? 'bg-orange-100 border-orange-300 text-orange-700 hover:bg-orange-200'
                    : 'bg-stone-200 border-stone-300 text-stone-400 cursor-not-allowed'
                }`}
                style={{
                  left: `${node.position.x}px`,
                  top: `${node.position.y}px`
                }}
              >
                <div className="text-xs font-medium text-center">
                  {node.isCompleted ? '‚úì' : node.category === 'Techniques' ? 'üîß' : node.category === 'Ingredients' ? 'üåø' : 'üìù'}
                </div>
              </button>
            ))}
            
            {/* Connection lines */}
            <svg className="absolute inset-0 w-full h-full pointer-events-none">
              <line x1="50" y1="80" x2="150" y2="60" stroke="#d6d3d1" strokeWidth="2" strokeDasharray="5,5" />
              <line x1="150" y1="60" x2="250" y2="40" stroke="#d6d3d1" strokeWidth="2" strokeDasharray="5,5" />
              <line x1="50" y1="80" x2="100" y2="120" stroke="#d6d3d1" strokeWidth="2" strokeDasharray="5,5" />
              <line x1="100" y1="120" x2="200" y2="100" stroke="#d6d3d1" strokeWidth="2" strokeDasharray="5,5" />
            </svg>
          </div>
        </Card>
      </div>

      {/* Selected Node Details */}
      {selectedNode && (
        <div className="px-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <span>
                  {selectedNode.category === 'Techniques' ? 'üîß' : selectedNode.category === 'Ingredients' ? 'üåø' : 'üìù'}
                </span>
                {selectedNode.title}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-stone-600 mb-4">{selectedNode.description}</p>
              <div className="flex items-center justify-between">
                <Badge variant="secondary">
                  Level {selectedNode.requiredLevel} required
                </Badge>
                <Button
                  size="sm"
                  disabled={selectedNode.isCompleted}
                  className="bg-orange-500 hover:bg-orange-600"
                >
                  {selectedNode.isCompleted ? 'Completed' : 'Start Lesson'}
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Badges Section */}
      <div className="px-4">
        <h2 className="text-xl font-semibold text-stone-800 mb-4">Achievement Badges</h2>
        
        {/* Earned Badges */}
        <div className="mb-6">
          <h3 className="text-sm font-medium text-stone-600 mb-3">Earned ({earnedBadges.length})</h3>
          <div className="grid grid-cols-3 gap-3">
            {earnedBadges.map((badge) => (
              <Card key={badge.id} className="p-3 text-center bg-gradient-to-br from-emerald-50 to-orange-50">
                <div className="text-2xl mb-1">{badge.icon}</div>
                <div className="text-xs font-medium text-stone-700">{badge.name}</div>
              </Card>
            ))}
          </div>
        </div>

        {/* Available Badges */}
        <div>
          <h3 className="text-sm font-medium text-stone-600 mb-3">Available ({availableBadges.length})</h3>
          <div className="grid grid-cols-3 gap-3">
            {availableBadges.map((badge) => (
              <Card key={badge.id} className="p-3 text-center bg-stone-50 opacity-60">
                <div className="text-2xl mb-1 grayscale">{badge.icon}</div>
                <div className="text-xs font-medium text-stone-500">{badge.name}</div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
