'use client';

import { useState } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { mockPosts } from '@/data/mock-data';
import { Post } from '@/types';

export default function KitchenFeed() {
  const [posts, setPosts] = useState<Post[]>(mockPosts);

  const handleLike = (postId: string) => {
    setPosts(posts.map(post => 
      post.id === postId 
        ? { 
            ...post, 
            isLiked: !post.isLiked,
            likes: post.isLiked ? post.likes - 1 : post.likes + 1
          }
        : post
    ));
  };

  const handleSave = (postId: string) => {
    setPosts(posts.map(post => 
      post.id === postId 
        ? { 
            ...post, 
            isSaved: !post.isSaved,
            saves: post.isSaved ? post.saves - 1 : post.saves + 1
          }
        : post
    ));
  };

  const formatTimeAgo = (date: Date) => {
    const now = new Date();
    const diffInHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60));
    
    if (diffInHours < 1) return 'Just now';
    if (diffInHours < 24) return `${diffInHours}h ago`;
    return `${Math.floor(diffInHours / 24)}d ago`;
  };

  return (
    <div className="space-y-4 pb-20">
      {/* Header */}
      <div className="bg-gradient-to-r from-emerald-50 to-orange-50 p-6 rounded-b-3xl">
        <h1 className="text-2xl font-bold text-stone-800">Kitchen Feed</h1>
        <p className="text-stone-600">Discover amazing dishes from the community</p>
      </div>

      {/* Feed */}
      <div className="px-4 space-y-6">
        {posts.map((post) => (
          <Card key={post.id} className="overflow-hidden border-0 shadow-sm">
            {/* User Header */}
            <div className="p-4 pb-3">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <div className="w-10 h-10 bg-gradient-to-br from-orange-400 to-emerald-400 rounded-full flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">
                      {post.user.displayName.charAt(0)}
                    </span>
                  </div>
                  <div>
                    <p className="font-semibold text-stone-800">{post.user.displayName}</p>
                    <p className="text-xs text-stone-500">@{post.user.username} ‚Ä¢ {formatTimeAgo(post.createdAt)}</p>
                  </div>
                </div>
                <Badge variant="secondary" className="text-xs">
                  Level {post.user.chefLevel}
                </Badge>
              </div>
            </div>

            {/* Image */}
            <div className="relative">
              <div className="aspect-square bg-gradient-to-br from-orange-100 via-stone-100 to-emerald-100 flex items-center justify-center">
                <div className="text-center">
                  <div className="text-6xl mb-2">üçΩÔ∏è</div>
                  <p className="text-stone-600 font-medium">{post.recipe.title}</p>
                </div>
              </div>
              
              {/* Recipe Info Overlay */}
              <div className="absolute top-3 left-3">
                <Badge className="bg-white/90 text-stone-700 border-0">
                  {post.recipe.cookingTime}min ‚Ä¢ {post.recipe.difficulty}
                </Badge>
              </div>
            </div>

            {/* Content */}
            <CardContent className="p-4">
              {/* Caption */}
              <p className="text-stone-700 mb-3">{post.caption}</p>

              {/* Tags */}
              <div className="flex flex-wrap gap-2 mb-4">
                {post.tags.map((tag) => (
                  <Badge key={tag} variant="outline" className="text-xs">
                    #{tag}
                  </Badge>
                ))}
              </div>

              {/* Actions */}
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <button
                    onClick={() => handleLike(post.id)}
                    className={`flex items-center space-x-1 transition-colors ${
                      post.isLiked ? 'text-red-500' : 'text-stone-500 hover:text-red-500'
                    }`}
                  >
                    <span className="text-lg">{post.isLiked ? '‚ù§Ô∏è' : 'ü§ç'}</span>
                    <span className="text-sm font-medium">{post.likes}</span>
                  </button>

                  <button
                    onClick={() => handleSave(post.id)}
                    className={`flex items-center space-x-1 transition-colors ${
                      post.isSaved ? 'text-orange-500' : 'text-stone-500 hover:text-orange-500'
                    }`}
                  >
                    <span className="text-lg">{post.isSaved ? 'üìå' : 'üìç'}</span>
                    <span className="text-sm font-medium">{post.saves}</span>
                  </button>

                  <button className="flex items-center space-x-1 text-stone-500 hover:text-emerald-500 transition-colors">
                    <span className="text-lg">üì§</span>
                    <span className="text-sm font-medium">{post.shares}</span>
                  </button>
                </div>

                <Button
                  size="sm"
                  variant="outline"
                  className="text-xs border-orange-200 text-orange-600 hover:bg-orange-50"
                >
                  View Recipe
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}

        {/* Load More */}
        <div className="text-center py-6">
          <Button variant="outline" className="w-full max-w-xs">
            Load More Posts
          </Button>
        </div>
      </div>
    </div>
  );
}
