'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { mockChallenges } from '@/data/mock-data';
import { Challenge, ChallengeEntry } from '@/types';

export default function ChefsShowdown() {
  const [selectedChallenge, setSelectedChallenge] = useState<Challenge | null>(null);
  const [votedEntries, setVotedEntries] = useState<Set<string>>(new Set());

  const handleVote = (entryId: string) => {
    if (!votedEntries.has(entryId)) {
      setVotedEntries(new Set([...votedEntries, entryId]));
      // In a real app, this would update the backend
    }
  };

  const formatTimeRemaining = (endDate: Date) => {
    const now = new Date();
    const diff = endDate.getTime() - now.getTime();
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    
    if (days > 0) return `${days}d ${hours}h left`;
    if (hours > 0) return `${hours}h left`;
    return 'Ending soon';
  };

  const ChallengeCard = ({ challenge }: { challenge: Challenge }) => (
    <Card 
      className="cursor-pointer hover:shadow-md transition-shadow"
      onClick={() => setSelectedChallenge(challenge)}
    >
      <CardContent className="p-0">
        {/* Challenge Header */}
        <div className="bg-gradient-to-r from-orange-100 to-emerald-100 p-4 rounded-t-lg">
          <div className="flex items-center justify-between mb-2">
            <Badge className="bg-white/80 text-orange-600 border-0">
              {challenge.isActive ? 'Active' : 'Ended'}
            </Badge>
            <span className="text-xs text-stone-600">
              {formatTimeRemaining(challenge.endDate)}
            </span>
          </div>
          <h3 className="font-bold text-lg text-stone-800 mb-1">{challenge.title}</h3>
          <p className="text-sm text-stone-600">{challenge.description}</p>
        </div>

        {/* Challenge Stats */}
        <div className="p-4">
          <div className="flex items-center justify-between mb-3">
            <div className="text-center">
              <div className="text-lg font-semibold text-stone-800">{challenge.participants.length}</div>
              <div className="text-xs text-stone-500">Participants</div>
            </div>
            <div className="text-center">
              <div className="text-lg font-semibold text-stone-800">{challenge.totalVotes}</div>
              <div className="text-xs text-stone-500">Total Votes</div>
            </div>
            <div className="text-center">
              <div className="text-lg">üèÜ</div>
              <div className="text-xs text-stone-500">Prize</div>
            </div>
          </div>

          {/* Top Entries Preview */}
          <div className="flex -space-x-2">
            {challenge.participants.slice(0, 3).map((participant, index) => (
              <div
                key={participant.id}
                className="w-8 h-8 bg-gradient-to-br from-orange-400 to-emerald-400 rounded-full border-2 border-white flex items-center justify-center"
              >
                <span className="text-white text-xs font-semibold">
                  {participant.user.displayName.charAt(0)}
                </span>
              </div>
            ))}
            {challenge.participants.length > 3 && (
              <div className="w-8 h-8 bg-stone-300 rounded-full border-2 border-white flex items-center justify-center">
                <span className="text-stone-600 text-xs">+{challenge.participants.length - 3}</span>
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );

  if (selectedChallenge) {
    return (
      <div className="space-y-4 pb-20">
        {/* Header */}
        <div className="bg-gradient-to-r from-orange-50 to-emerald-50 p-6 rounded-b-3xl">
          <div className="flex items-center justify-between mb-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setSelectedChallenge(null)}
              className="text-stone-600 hover:text-stone-800"
            >
              ‚Üê Back to Challenges
            </Button>
            <Badge className={selectedChallenge.isActive ? 'bg-emerald-100 text-emerald-700' : 'bg-stone-100 text-stone-700'}>
              {selectedChallenge.isActive ? 'Active' : 'Ended'}
            </Badge>
          </div>
          <h1 className="text-2xl font-bold text-stone-800">{selectedChallenge.title}</h1>
          <p className="text-stone-600">{selectedChallenge.description}</p>
          <p className="text-sm text-stone-500 mt-2">
            {formatTimeRemaining(selectedChallenge.endDate)}
          </p>
        </div>

        {/* Challenge Stats */}
        <div className="px-4">
          <Card>
            <CardContent className="p-4">
              <div className="grid grid-cols-3 gap-4 text-center">
                <div>
                  <div className="text-xl font-bold text-stone-800">{selectedChallenge.participants.length}</div>
                  <div className="text-xs text-stone-500">Participants</div>
                </div>
                <div>
                  <div className="text-xl font-bold text-stone-800">{selectedChallenge.totalVotes}</div>
                  <div className="text-xs text-stone-500">Total Votes</div>
                </div>
                <div>
                  <div className="text-xl">üèÜ</div>
                  <div className="text-xs text-stone-500">Winner Badge</div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Voting Section */}
        <div className="px-4">
          <h2 className="text-lg font-semibold text-stone-800 mb-4">Vote for Your Favorite</h2>
          <div className="space-y-4">
            {selectedChallenge.participants.map((entry) => (
              <Card key={entry.id} className="overflow-hidden">
                <CardContent className="p-0">
                  {/* Entry Image */}
                  <div className="aspect-square bg-gradient-to-br from-orange-100 via-stone-100 to-emerald-100 flex items-center justify-center">
                    <div className="text-center">
                      <div className="text-6xl mb-2">üçΩÔ∏è</div>
                      <p className="text-stone-600 font-medium">{entry.dish}</p>
                    </div>
                  </div>

                  {/* Entry Info */}
                  <div className="p-4">
                    <div className="flex items-center justify-between mb-3">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-gradient-to-br from-orange-400 to-emerald-400 rounded-full flex items-center justify-center">
                          <span className="text-white text-xs font-semibold">
                            {entry.user.displayName.charAt(0)}
                          </span>
                        </div>
                        <div>
                          <p className="font-semibold text-stone-800">{entry.user.displayName}</p>
                          <p className="text-xs text-stone-500">@{entry.user.username}</p>
                        </div>
                      </div>
                      <Badge variant="secondary">
                        Level {entry.user.chefLevel}
                      </Badge>
                    </div>

                    <h3 className="font-semibold text-stone-800 mb-2">{entry.dish}</h3>

                    {/* Voting Progress */}
                    <div className="mb-3">
                      <div className="flex items-center justify-between mb-1">
                        <span className="text-sm text-stone-600">{entry.votes} votes</span>
                        <span className="text-sm font-medium text-stone-700">{entry.votePercentage}%</span>
                      </div>
                      <Progress value={entry.votePercentage} className="h-2" />
                    </div>

                    {/* Vote Button */}
                    <Button
                      onClick={() => handleVote(entry.id)}
                      disabled={votedEntries.has(entry.id)}
                      className={`w-full ${
                        votedEntries.has(entry.id)
                          ? 'bg-emerald-500 hover:bg-emerald-600'
                          : 'bg-orange-500 hover:bg-orange-600'
                      }`}
                    >
                      {votedEntries.has(entry.id) ? '‚úì Voted' : 'Vote for this dish'}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>

        {/* Join Challenge */}
        {selectedChallenge.isActive && (
          <div className="px-4">
            <Card className="bg-gradient-to-r from-orange-50 to-emerald-50">
              <CardContent className="p-6 text-center">
                <div className="text-4xl mb-3">üë®‚Äçüç≥</div>
                <h3 className="font-semibold text-stone-800 mb-2">Join the Challenge!</h3>
                <p className="text-sm text-stone-600 mb-4">
                  Cook your version of {selectedChallenge.theme} and compete for the top spot
                </p>
                <Button className="bg-orange-500 hover:bg-orange-600">
                  Submit My Entry
                </Button>
              </CardContent>
            </Card>
          </div>
        )}
      </div>
    );
  }

  return (
    <div className="space-y-6 pb-20">
      {/* Header */}
      <div className="bg-gradient-to-r from-orange-50 to-emerald-50 p-6 rounded-b-3xl">
        <h1 className="text-2xl font-bold text-stone-800">Chef's Showdown</h1>
        <p className="text-stone-600">Compete with fellow chefs and showcase your skills</p>
      </div>

      {/* Challenge Tabs */}
      <div className="px-4">
        <Tabs defaultValue="active" className="w-full">
          <TabsList className="grid w-full grid-cols-3 bg-stone-100">
            <TabsTrigger value="active" className="text-xs">Active</TabsTrigger>
            <TabsTrigger value="upcoming" className="text-xs">Upcoming</TabsTrigger>
            <TabsTrigger value="completed" className="text-xs">Completed</TabsTrigger>
          </TabsList>

          <TabsContent value="active" className="space-y-4 mt-6">
            {mockChallenges.filter(c => c.isActive).map((challenge) => (
              <ChallengeCard key={challenge.id} challenge={challenge} />
            ))}
          </TabsContent>

          <TabsContent value="upcoming" className="space-y-4 mt-6">
            <div className="text-center py-8 text-stone-500">
              <div className="text-4xl mb-2">‚è∞</div>
              <p>New challenges coming soon!</p>
            </div>
          </TabsContent>

          <TabsContent value="completed" className="space-y-4 mt-6">
            <div className="text-center py-8 text-stone-500">
              <div className="text-4xl mb-2">üèÜ</div>
              <p>Completed challenges will appear here</p>
            </div>
          </TabsContent>
        </Tabs>
      </div>

      {/* Create Challenge */}
      <div className="px-4">
        <Card className="bg-gradient-to-r from-emerald-50 to-orange-50">
          <CardContent className="p-6 text-center">
            <div className="text-4xl mb-3">‚öîÔ∏è</div>
            <h3 className="font-semibold text-stone-800 mb-2">Start Your Own Challenge</h3>
            <p className="text-sm text-stone-600 mb-4">
              Challenge friends or the community with your favorite theme
            </p>
            <Button variant="outline" className="border-orange-200 text-orange-600 hover:bg-orange-50">
              Create Challenge
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
